import React, { ReactElement, useEffect, useState } from 'react';
import {
  WhiteSpace,
  Picker,
  Steps,
  DatePicker,
  Drawer,
  InputItem,
  List,
  Toast,
} from 'antd-mobile';
import { useHistory } from 'react-router-dom';

import PickItem from '@/components/PickItem';
import {
  gender,
  degree,
  japaneseAbilityCertificate,
  englishAbilityCertificate,
} from '@/constants/globalDatas';
import district from '@/constants/district.json';
import { useMutation, useQuery } from '@apollo/client';
import APIHelper, { UPDATE_USER_INFO, USER_SHOW } from '@/helpers/APIHelper';
import getGoalSchool, { PickDataItem } from '@/utils/getGoalSchool';
import {
  Page,
  PageTitle,
  OthersItem,
  PrimaryButton,
  DrawerBox,
  ButtonBox,
} from '../styles';

const { Step } = Steps;
const CompleteAll: React.FC = () => {
  const [birthday, setBirthday] = useState(''); // 出生日期
  const [genderValue, setGenderValue] = useState<number[]>([]); // 性别
  const [regionValue, setRegionValue] = useState<string[]>([]); // 国内地区
  const [degreeValue, setDegreeValue] = useState<string[]>([]); // 最终学历
  const [goalSchoolValue, setGoalSchoolValue] = useState<string[]>([]); // 目标院校
  const [goalSchool, setGoalSchool] = useState<PickDataItem[]>([]); // 目标院校列表
  const [japaneseAbilityCertificateValue, setJapaneseAbilityCertificateValue] =
    useState<string[]>([]); // 日语能力
  const [japaneseAbilityPointValue, setJapaneseAbilityPointValue] = useState(0); // 日语成绩
  const [englishAbilityCertificateValue, setEnglishAbilityCertificateValue] =
    useState<string[]>([]); // 英语能力
  const [englishAbilityPointValue, setEnglishAbilityPointValue] = useState(0); // 英语成绩
  const [drawerOpen, setDraweOpen] = useState(false); // 是否打开最高留考成绩抽屉
  const [ejuValue, setEjuValue] = useState({}); // 留考成绩展示
  const [ejuValuePoint, setEjuValuePoint] = useState(''); // 留考成绩总分
  const history = useHistory();
  const { data: userInfo } = useQuery(USER_SHOW, {
    fetchPolicy: 'cache-and-network',
  });
  const [updateUserInfo] = useMutation(UPDATE_USER_INFO, {
    onCompleted: () => history.push('/complete-show'),
    onError: () => Toast.show('保存失败'),
  });

  // 格式化日期格式
  const formatDate = (date: Date): string => {
    const pad = (n: number): string | number => (n < 10 ? `0${n}` : n);
    const dateStr = `${date.getFullYear()}-${pad(date.getMonth() + 1)}-${pad(
      date.getDate()
    )}`;
    return dateStr;
  };
  // 设置下拉选中的值
  const handleSelectPick = (value: any, type: string): void => {
    console.log(value.toString());
    switch (type) {
      case 'birthday':
        setBirthday(formatDate(value));
        break;
      case 'gender':
        setGenderValue(value);
        break;
      case 'region':
        setRegionValue(value);
        break;
      case 'degree':
        setDegreeValue(value);
        break;
      case 'japaneseAbilityCertificate':
        setJapaneseAbilityCertificateValue(value);
        break;
      case 'englishAbilityCertificate':
        setEnglishAbilityCertificateValue(value);
        break;
      case 'goalSchool':
        setGoalSchoolValue(value);
        break;
      default:
    }
  };

  // 下一步前进行提交
  const handleSubmit = (): void => {
    const ejuPoint: { subject: string; point: number }[] = [];
    Object.keys(ejuValue).forEach((item) => {
      ejuPoint.push({
        subject: item,
        point: Number(ejuValue[item as keyof typeof ejuValue]),
      });
    });
    const param = {
      birthday,
      gender: genderValue[0],
      region: regionValue,
      degree: degreeValue[0],
      japaneseAbility: {
        certificate: japaneseAbilityCertificateValue[0],
        point: japaneseAbilityPointValue,
      },
      englishAbility: {
        certificate: englishAbilityCertificateValue[0],
        point: englishAbilityPointValue,
      },
      goalSchool: goalSchoolValue[0],
      ejuPoint,
    };
    updateUserInfo({
      variables: {
        userInfoInput: param,
      },
    });
  };
  // 修改分数
  const handleEjuPoint = (points: string, type: string): void => {
    setEjuValue((val) => {
      const newVal = { ...val, [type]: points };
      return newVal;
    });
  };

  // 填写留考成绩
  const handleEjuValue = (): void => {
    let totalPoint = 0;
    Object.keys(ejuValue).forEach((item) => {
      totalPoint += Number(ejuValue[item as keyof typeof ejuValue]);
    });
    setEjuValuePoint(totalPoint.toString());
    setDraweOpen(false);
  };
  const ejuContent = (): ReactElement => {
    console.log('userInfo+++++', userInfo);
    // 0文科  1理科
    const artScience = userInfo?.getUserInfo?.artScience;
    return (
      <DrawerBox>
        <List renderHeader="留考最高成绩">
          {artScience === 0 && (
            <>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void =>
                  handleEjuPoint(val, 'japWriting')
                }
              >
                日语写作
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void =>
                  handleEjuPoint(val, 'japReading')
                }
              >
                日语阅读
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void =>
                  handleEjuPoint(val, 'japListening')
                }
              >
                日语听力
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void => handleEjuPoint(val, 'math1')}
              >
                数学1
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void => handleEjuPoint(val, 'ACAT')}
              >
                文综
              </InputItem>
            </>
          )}
          {artScience === 1 && (
            <>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void =>
                  handleEjuPoint(val, 'japWriting')
                }
              >
                日语写作
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void =>
                  handleEjuPoint(val, 'japReading')
                }
              >
                日语阅读
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void =>
                  handleEjuPoint(val, 'japListening')
                }
              >
                日语听力
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void => handleEjuPoint(val, 'math2')}
              >
                数学2
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void => handleEjuPoint(val, 'physics')}
              >
                物理
              </InputItem>
              <InputItem
                placeholder="请输入成绩"
                type="number"
                onChange={(val: string): void =>
                  handleEjuPoint(val, 'chemistry')
                }
              >
                化学
              </InputItem>
            </>
          )}
        </List>
        <WhiteSpace size="xl" />
        <PrimaryButton type="primary" onClick={(): void => handleEjuValue()}>
          保存
        </PrimaryButton>
      </DrawerBox>
    );
  };
  useEffect(() => {
    // 初始化获得目标院校
    APIHelper.getSchool().then((res) => {
      const filterArr = getGoalSchool(res.data);
      setGoalSchool(filterArr);
    });
  }, []);
  return (
    <>
      <Page>
        <PageTitle>请完善您的信息</PageTitle>
        <WhiteSpace size="xl" />
        <Steps direction="horizontal" current={1}>
          <Step />
          <Step />
          <Step />
        </Steps>
        <WhiteSpace size="xl" />
        <OthersItem>选填信息（填写该信息开启AI考学分析功能）</OthersItem>
        <WhiteSpace size="xl" />
        <DatePicker
          mode="date"
          onChange={(value): void => handleSelectPick(value, 'birthday')}
          format="YYYY-MM-DD"
        >
          <PickItem placeholder="请选择出生日期" value={birthday} />
        </DatePicker>
        <Picker
          data={gender}
          cols={1}
          value={genderValue}
          onOk={(value: string[]): void => handleSelectPick(value, 'gender')}
        >
          <PickItem placeholder="请选择性别" />
        </Picker>
        <Picker
          data={district.data}
          value={regionValue}
          onOk={(value: string[]): void => handleSelectPick(value, 'region')}
        >
          <PickItem placeholder="请选择国内地区" />
        </Picker>
        <Picker
          data={degree}
          cols={1}
          value={degreeValue}
          onOk={(value: string[]): void => handleSelectPick(value, 'degree')}
        >
          <PickItem placeholder="请选择最终学历" />
        </Picker>
        <Picker
          data={japaneseAbilityCertificate}
          cols={1}
          value={japaneseAbilityCertificateValue}
          onOk={(value: string[]): void =>
            handleSelectPick(value, 'japaneseAbilityCertificate')
          }
        >
          <PickItem placeholder="请选择日语能力" />
        </Picker>
        {japaneseAbilityCertificateValue.length > 0 && (
          <>
            <InputItem
              placeholder="请输入日语成绩"
              type="number"
              onChange={(val): void =>
                setJapaneseAbilityPointValue(Number(val))
              }
            />
            <WhiteSpace size="lg" />
          </>
        )}
        <Picker
          data={englishAbilityCertificate}
          cols={1}
          value={englishAbilityCertificateValue}
          onOk={(value: string[]): void =>
            handleSelectPick(value, 'englishAbilityCertificate')
          }
        >
          <PickItem placeholder="请选择英语能力" />
        </Picker>
        {englishAbilityCertificateValue.length > 0 && (
          <>
            <InputItem
              placeholder="请输入英语成绩"
              type="number"
              onChange={(val): void => setEnglishAbilityPointValue(Number(val))}
            />
            <WhiteSpace size="lg" />
          </>
        )}
        <Picker
          data={goalSchool}
          cols={1}
          value={goalSchoolValue}
          onOk={(value: string[]): void =>
            handleSelectPick(value, 'goalSchool')
          }
        >
          <PickItem placeholder="请选择目标院校" />
        </Picker>

        <PickItem
          placeholder="请输入留考最高成绩"
          onClick={(): void => setDraweOpen(true)}
          value={ejuValuePoint}
        />

        <ButtonBox>
          <PrimaryButton
            inline
            type="ghost"
            onClick={(): void => history.push('/')}
          >
            跳过
          </PrimaryButton>

          <PrimaryButton
            inline
            type="primary"
            onClick={(): void => handleSubmit()}
          >
            下一步
          </PrimaryButton>
        </ButtonBox>
      </Page>
      {drawerOpen && (
        <Drawer
          sidebar={ejuContent()}
          position="bottom"
          open={drawerOpen}
          enableDragHandle
          onOpenChange={(): void => setDraweOpen(!drawerOpen)}
        >
          <div />
        </Drawer>
      )}
    </>
  );
};

export default CompleteAll;
